@import url(https://fonts.googleapis.com/css?family=Mr+Dafoe&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sacramento&display=swap);
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e8e8e8;
  line-height: 1.8;
  font-size: 18px;
}


h1, h2, h3, h4 {
  font-family: 'Lato', sans-serif;
  font-weight: 900;
}

h5 {
  font-size: 28px; 
}

h6 {
  font-size: 24px;
}

.dark-purple-text {
  color: #4a148c;
}

.mint-text {
  color: #4db6ac; 
}

.heading {
  text-align: center; 
}

.heading > h4 > i.heading-icons {
    border: 3px solid #8e24aa;
    border-radius: 50px;
    padding: 5px;
    background-color: #8e24aa;
    color: white;
    margin-bottom: 10px;
}

strong {
  font-weight: 900;
}


.body-wrapper {
  background-color: #e8e8e8;
  min-height: 100vh;
}


.hero-gradient {
    position: absolute; 
    top: 0; 
    left: 0; 
    height: 80vh;
    width: 99vw;
    z-index: 0;
    background-image: -webkit-gradient(linear , left top, left bottom , from(rgba(128, 203, 196, 0.8)) , to(rgba(123, 31, 162, 0.6))), url("/assets/images/foggy-forest.jpg");
    background-image: linear-gradient(rgba(128, 203, 196, 0.8) , rgba(123, 31, 162, 0.6)), url("/assets/images/foggy-forest.jpg");
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
}


.hero-title {
    position: absolute; 
    top: 0; 
    left: 0;
    width: 100%;
    height: 100%; 
    z-index: 10; 
    display: flex; 
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 0;
    font-family: 'Nanum Gothic', sans-serif;
    font-weight: 400;
    text-align: center;
}

.hero-title > h2 {
    color: #ffffff;
    font-size: 9rem;
    letter-spacing: 2px;
    margin-top: 0;
    margin-bottom: 30px;
    font-family: 'Mr Dafoe', cursive;
    -webkit-text-stroke: 1px #4a148c;
    -webkit-transform: rotate(-9deg);
            transform: rotate(-9deg);
    -ms-transform: rotate(-20deg);
}

.hero-title > span {
    font-size: 2.7rem;
    color: #ffffff;
    font-weight: 900;
    line-height: 1.2;

}

.fog {
    position: relative; 
    height: 80vh; 
    width: 100%; 
    background: #ffffff;
    margin-bottom: 60px;
}

.fog-container {
    position: absolute; 
    width: 100%;
    height: 100%;
    overflow: hidden; 
    z-index: 1;
}

.fog-img {
    position: absolute; 
    height: 100vh;
    width: 300vw; 
    z-index: 2;
}

.fog-img-first {
    background-image: url("/assets/images/fog-1.png");
    background-repeat: repeat-x;
    background-size: contain; 
    background-position: center; 
    -webkit-animation: marquee 60s linear infinite; 
            animation: marquee 60s linear infinite; 
}

.fog-img-second {
    background-image: url("/assets/images/fog-2.png");
    background-repeat: repeat-x;
    background-size: contain; 
    background-position: center; 
    -webkit-animation: marquee 60s linear infinite; 
            animation: marquee 60s linear infinite; 
}

.svg-triangle{ 
    position: absolute;
    top: 64vh; 
    width: 100%;
    height: 20%;
    z-index: 5;
}

.svg--sm{
    display: none;
}

@-webkit-keyframes marquee {
    0% {
        -webkit-transform: translate3d(0,0,0,);
                transform: translate3d(0,0,0,);
    }
    100% {
        -webkit-transform: translate3d(-200vw,0, 0);
                transform: translate3d(-200vw,0, 0);
    }
}

@keyframes marquee {
    0% {
        -webkit-transform: translate3d(0,0,0,);
                transform: translate3d(0,0,0,);
    }
    100% {
        -webkit-transform: translate3d(-200vw,0, 0);
                transform: translate3d(-200vw,0, 0);
    }
}
@media (max-width: 699px) {
    .svg--lg {
      display: none;
    }

    .svg--sm {
        width: 100%;
        top: 76vh;
    }

    .svg-triangle {
        top: 75vh;
    }

    .hero-title {
        position: relative; 
        top: 0; 
        left: 5vw;
        width: 93%;
        text-align: center;
    }

    .hero-title > h2 {
        font-size: 4.5rem;
    }

    .hero-title > span {
    /* padding-left: 10px; */
    font-family: 'Lato', sans-serif !important;
    }

    .fog-container {
    height: 90vh;
    }

    .fog-img-first {
            height: 90vh;
    }

    .fog-img-second {
        height: 90vh;
}

    .hero-gradient {
        height: 90vh;
        margin-bottom: 50px;
        width: 100%;
    }

    
  }
  
  @media (min-width: 700px) {
    .svg-sm {
      display: none;
    }
  }
